@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  font-family: 'Open Sans', sans-serif;
  margin: 0 auto;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  margin: 0 auto;
  padding: 0;
  text-align: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-image: url(https://image.winudf.com/v2/image1/Y29tLm9sZWdydWJub3YuQ2hlY2tlcnNTcG9ydE5ld1dhbGxwYXBlcnMyMDE4X3NjcmVlbl80XzE1NDE1MzUwODhfMDAw/screen-4.jpg?fakeurl=1&type=.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

content{
  width:100%;
  height:100%;
}

.app .list-game{
  width: 100%;
  height: 100%;
}

content .header{
  top:0;
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.513);
}

content .header h2{
margin: 0 10%;
color: white;
text-shadow: 2px 2px 2px black;
}

content .header .create-game{
 padding: 0 2%;
 border-right: 2px solid white;
 border-left: 2px solid white;
}

content .header .create-game:hover{
color: black;
cursor: pointer;
text-shadow: 2px 2px 2px grey;
background-color: white;
border-radius: 2px;
}

button{
  font-family: 'Open Sans', sans-serif;
  border-radius: 5px;
}

.login-form{
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    background-image: url(https://i2.rozetka.ua/goods/8643798/61076727_images_8643798245.jpg);
    width: 20%;
    border: 3px solid black;
    color: white;
    font-weight: 400;
}
.login-form label{
    display: block;
    margin: 0 auto;
    width: 200px;
}
input{
    font-family: 'Open Sans', sans-serif;
    border-radius: 5px;
    border: none;
    background: none;
    font-size: 20px;
    color: black;
    text-shadow: 0px 0px 3px white;
    font-weight: 800;
}
.login-form label input{
border: 1px solid black;
background: white;
}
input:hover{
    color: white;
    text-shadow: 0px 0px 2px black;
    cursor: pointer;
}


.wait-block{
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    z-index: 99;
}

.wait-block .wait-message{
    width: 15%;
    height: 13%;
    text-align: center;
    margin: 0 auto;
    color: black;
    background-image: url(https://i2.rozetka.ua/goods/8643798/61076727_images_8643798245.jpg);
    border-radius: 5px;
    border: 2px solid black;
    color:  white;
    text-shadow: 0px 0px 2px wheat;
}

.game{
    display: block;
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    background-image: url(https://oboi.ws/filters/toaster_17_8440_oboi_derevjannyj_pol_1920x1080.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}
.board{
    width: 785px;
    height: 782px;
    background-color: black;
    margin: 0 auto;
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
    flex-direction: row;
    border: 20px solid;
    -webkit-border-image: url(https://sale-floor.ru/images/product_images/info_images/135_0.jpg);
            border-image: url(https://sale-floor.ru/images/product_images/info_images/135_0.jpg);
    border-image-width: 100%;
    border-image-repeat: repeat;
}
.field{
    width: 96px;
    height: 96px;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.field_black{
    background-image: url(https://cpimg.tistatic.com/04098671/s/4/Ebony-Oak-plank.jpg);
}

.field_white{
    background-image: url(http://cgtexture.ir/images/products/thumb/108057sbbftb.jpg);
}

.field_light{
    background-color: rgba(0, 255, 42, 1);
}
.check{
    margin: 0 auto;
    width: 82px;
    height: 84px;
    border-radius: 50%;
}

.check_click{
    box-shadow: 0 0 20px rgba(0, 255, 42, 1);
    border: 1px solid rgba(0, 255, 42, 1);
}

.check_black{
    background-image: url(http://forum-psn.ru/uploads/monthly_09_2018/post-289-1536310601.png);
    background-size: cover;
    background-color: black;
}

.check_white{
    background-image: url(https://yt3.ggpht.com/a/AGF-l7-lhAcO1brBZbmXIiD4kpc6drBwABWVYQ7eSw=s400-c-k-c0xffffffff-no-rj-mo);
    background-size: cover;
    background-color: burlywood;
    background-position-x: center;
}
.chat{
    position: fixed;
    background: white;
    height: 70%;
    width: 20%;
    border-radius: 5px;
    border: 2px solid black;
    margin: 5% 0;
    display: flex;
    flex-direction: column;
    z-index: 100;
}

.chat .messages{
    margin:0 10px;
    width: 100%;
    height: 95%;
    text-align: left;
}

.chat .message{
    width: 100%;
    height: 5%;
    display: flex;
    flex-direction: row;
    border-top: 2px solid grey;
}

.chat .message input{
background-color: black;
margin: 0 5px;
width: 80%;
border: 2px solid whitesmoke;
color:  white;
}

.chat .message button{
    font-family: 'Open Sans', sans-serif;
    border-radius: 5px;
    border: none;
    background: none;
    font-size: 20px;
    color: black;
    text-shadow: 0px 0px 3px white;
    font-weight: 800;
    cursor: pointer;
}

.chat .message button:hover{
    color: white;
    text-shadow: 0px 0px 3px black;
}
.game-list{
    margin: 10% auto 0 auto;
    background-image: url(https://i2.rozetka.ua/goods/8643798/61076727_images_8643798245.jpg);
    width: 50%;
    height: 50%;
    border-radius: 5px;
    border: 2px solid black;
}

.game-list .line{
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    color: white;
    border-bottom: 1px solid white;
    background-color: rgba(128, 128, 128, 0.486);
}

.game-list .line:hover{
background: rgba(255, 255, 255, 0.651);
color:black;
cursor: pointer;
}

.game-list .header-game-list{
    display: flex;
    justify-content: space-between;
    color: white;
    background-color: rgba(0, 0, 0, 0.507);
    
}

.game-list .header-game-list span, .game-list .line span{
padding: 0 10%;
width: 30%;
}
.modal {
    position: fixed;
    text-align: center;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.596);
  }

  .modal .modal-border{
      width: 20%;
      height: 50%;
      background-color: white;
      margin: 0 auto;
  }
